import React, { useEffect, useState } from 'react'
import { icons } from '../utilits/icons'
import { Link } from 'react-router-dom'

export default function Showmap() {
    const [location, setlocation] = useState([])

    useEffect(() => {
        const fetchData = async () => {
            try {
                const response = await fetch('http://81.200.149.8:8080/product/product/');
                if (!response.ok) {
                    throw new Error('Network response was not ok');
                }
                const data = await response.json();
                setlocation(data)
            } catch (error) {
                console.error('Error fetching data:', error);
            }
        }

        fetchData();
    }, []);
    // console.log(location);


    return (
        <>
            <div className='cursor-grab'>
                {location.map((element, value) => (
                    <iframe className='flex justify-center m-auto' src={`https://www.google.com/maps/embed?pb=!1m14!1m12!1m3!1d5991.074133283874!2d${element.longitude}!3d${element.latitude}!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!5e0!3m2!1suz!2s!4v1708429866184!5m2!1suz!2s`} width="95%" height="650" allowfullscreen="" loading="lazy" referrerpolicy="no-referrer-when-downgrade"></iframe>

                ))}
            </div>
            <div className="fixed top-[80%] left-[50%] translate-x-[-50%] ">
                <Link to={'/'} className='flex items-center gap-[10px] text-white bg-black px-[20px] py-[10px] rounded-[20px]'>Show list <span>{icons.list}</span></Link>
            </div>
        </>
    )
}
