import React, { useContext, useEffect, useState } from 'react'
import rasm1 from '../assets/img/11.webp'
import rasm2 from '../assets/img/12.webp'
import rasm3 from '../assets/img/13.webp'
import rasm4 from '../assets/img/14.webp'
import { icons } from '../utilits/icons'
import { Link } from 'react-router-dom'
import { Swiper, SwiperSlide } from 'swiper/react';
import { Pagination, Navigation } from 'swiper/modules';
import 'swiper/css';
import 'swiper/css/pagination';
import 'swiper/css/navigation';
import { Contexts } from '../context/Context'

export default function Product() {
    const { islogin, setislogin, scrolled, setScrolled, product, setproduct, catagoryid, setcatagoryid, activefooter, setActivefooter, modalnav, setmodalnav, } = useContext(Contexts)
    const [toggle, settoggle] = useState(false)
    const [footer, setfooter] = useState(false);
    const [swiperbutton, setswiperbutton] = useState(false);
    const swich = () => {
        settoggle(!toggle);
    };
    const handlefooter = (index) => {
        setfooter(index)
    }
    const [currentImageIndex, setCurrentImageIndex] = useState(0);
    const images = [rasm1, rasm2, rasm3, rasm4, rasm1, rasm2, rasm3,];

    const nextImage = () => {
        setCurrentImageIndex((prevIndex) => (prevIndex === images.length - 1 ? 0 : prevIndex + 1));

    };
    const prevImage = () => {
        setCurrentImageIndex((prevIndex) => (prevIndex === 0 ? images.length - 1 : prevIndex - 1));

    };
    const [isHovered, setIsHovered] = useState(false);
    const [like, setlike] = useState(false);


    const [activemap, setActivemap] = useState(false);
    useEffect(() => {
        const handleScroll = () => {
            const scrollPos = window.scrollY;
            if (scrollPos >= 200) {
                setActivefooter(false);
                setActivemap(true)
            } else {
                setActivefooter(true)
                setActivemap(false)
            }

        };

        window.addEventListener('scroll', handleScroll);

        return () => {
            window.removeEventListener('scroll', handleScroll);
        };
    }, []);
    const setproductid = (productId) => {

        localStorage.setItem('productId', productId);
    };
    useEffect(() => {
        const fetchData = async () => {
            try {
                const response = await fetch('http://81.200.149.8:8080/product/product/');
                if (!response.ok) {
                    throw new Error('Network response was not ok');
                }
                const data = await response.json();
                setproduct(data);
            } catch (error) {
                console.error('Error fetching data:', error);
            }
        }

        fetchData();
    }, []);
    console.log(product);
    const [categoryProducts, setCategoryProducts] = useState([]);
    useEffect(() => {
        if (catagoryid !== null) {
            const categoryProducts = product.filter(item => item.category.id === catagoryid);
            setCategoryProducts(categoryProducts);
        }
    }, [catagoryid, product]);

    // console.log(categoryProducts);
    return (
        <>
            <div className={scrolled ? 'lg:px-[80px] px-[10px] mt-[210px]' : 'lg:px-[80px] px-[10px]'}>
                {categoryProducts.length > 0 ? <div className="py-[10px] flex flex-wrap  gap-[20px] mt-[0px]">
                    {categoryProducts && categoryProducts.map((element, value) => (
                        <Link onClick={() => setproductid(element.id)} className="py-[10px] relative" key={value} onMouseEnter={() => setswiperbutton(true)} onMouseLeave={() => setswiperbutton(false)} >
                            <Swiper
                                spaceBetween={50}
                                slidesPerView={1}
                                className='lg:h-[305px] h-[280px] lg:w-[320px] w-[300px] '
                                pagination={{
                                    dynamicBullets: true,
                                }}

                                grabCursor={true}
                                navigation={{
                                    prevEl: ".swiper-button-prev",
                                    nextEl: ".swiper-button-next",
                                }}

                                modules={[Pagination, Navigation]}

                            >
                                {swiperbutton ? <div className="">
                                    <button className="swiper-button-prev lg:block hidden"></button>
                                    <button className="swiper-button-next lg:block hidden"></button>
                                </div> : null}
                                {element.images && element.images.map((rasmlar, index) => (
                                    <SwiperSlide key={index}>
                                        <div className="lg:w-full h-full rounded-xl bg-no-repeat bg-center" style={{ backgroundImage: `url(${rasmlar})` }}>
                                        </div>
                                    </SwiperSlide>
                                ))}
                            </Swiper>
                            <div className="flex items-center justify-end p-[10px] absolute top-3 z-10 w-full">
                                {/* <button className='py-[5px] px-[10px] text-[16px] font-semibold bg-white rounded-[20px]'>Guest favorite</button> */}
                                <button onClick={() => setlike(!like)} className='self-end'>{like ? <span>{icons.likered}</span> : <span>{icons.likeb}</span>}</button>
                            </div>
                            <div className=" mt-[10px] grid px-[10px]">
                                <h2 className='text-[16px] leading-[20px] font-semibold'>{element.name}</h2>
                                <h3 className='text-[16px] leading-[20px] text-gray-600'>{element.price}  so'm</h3>
                                <p className='text-[16px] leading-[20px] text-gray-600'>{element.description}</p>
                                <h4 className='text-[16px] leading-[20px] text-gray-600'><span className='text-black font-semibold'>${element.price_currency}</span> night</h4>
                            </div>
                        </Link>
                    ))}
                </div> : <div className="py-[10px] flex flex-wrap  gap-[20px] mt-[0px] opacity-50">

                    <Link  className="py-[10px] relative"  >
                        <Swiper
                            spaceBetween={50}
                            slidesPerView={1}
                            className='lg:h-[305px] h-[280px] lg:w-[320px] w-[300px] bg-gray-200 rounded-xl'
                            pagination={{
                                dynamicBullets: true,
                            }}

                            grabCursor={true}
                            navigation={{
                                prevEl: ".swiper-button-prev",
                                nextEl: ".swiper-button-next",
                            }}

                            modules={[Pagination, Navigation]}

                        >
                            {swiperbutton ? <div className="">
                                <button className="swiper-button-prev lg:block hidden"></button>
                                <button className="swiper-button-next lg:block hidden"></button>
                            </div> : null}

                            <SwiperSlide>
                                <div className="lg:w-full h-full rounded-xl bg-no-repeat bg-center" style={{ backgroundImage: `url($})` }}>
                                </div>
                            </SwiperSlide>

                        </Swiper>
                        <div className="flex items-center justify-end p-[10px] absolute top-3 z-10 w-full">
                            {/* <button className='py-[5px] px-[10px] text-[16px] font-semibold bg-white rounded-[20px]'>Guest favorite</button> */}
                            <button onClick={() => setlike(!like)} className='self-end opacity-0'>{like ? <span>{icons.likered}</span> : <span>{icons.likeb}</span>}</button>
                        </div>
                        <div className=" mt-[10px] grid gap-[5px] px-[10px]">
                            <h2 className='text-[16px] leading-[20px] font-semibold text-gray-200 bg-gray-200 w-[180px]'>rthrthrt</h2>
                            <h3 className='text-[16px] leading-[20px] text-gray-200 bg-gray-200 w-[140px]'> so'm</h3>
                            <p className='text-[16px] leading-[20px] text-gray-200 bg-gray-200 w-[100px]'>argsegrth</p>
                            <h4 className='text-[16px] leading-[20px] text-gray-200 bg-gray-200 w-[80px]'> night</h4>
                        </div>
                    </Link>

                </div>}
            </div >
            {activemap && <div className="fixed top-[80%] left-[50%] translate-x-[-50%] z-20">
                <Link to={'/showmap'} className='flex items-center gap-[10px] lg:text-[18px] text-[12px] text-white bg-black px-[20px] py-[10px] rounded-[20px]'><span className='lg:block hidden'>Show</span> Map <span>{icons.map}</span></Link>
            </div>
            }
            {
                modalnav ? null : <div className=" lg:hidden block">
                    {activefooter && <div className=" fixed bottom-0 py-[10px] border-t-[1px] border-gray-300 flex justify-center bg-white w-full z-30">
                        <div className="flex gap-[30px] text-center items-center">
                            <div onClick={() => handlefooter(0)} className={footer === 0 ? 'grid text-red-500' : 'grid'}>
                                <span className='fa fa-search'></span>
                                <p className='text-[12px]'>Explore</p>
                            </div>
                            <div onClick={() => handlefooter(1)} className={footer === 1 ? 'grid text-red-500' : 'grid'}>
                                <span className='fa fa-heart'></span>
                                <p className='text-[12px]'>Wishlists</p>
                            </div>
                            <div className="">
                                <div onClick={() => handlefooter(2)} className={footer === 2 ? 'grid text-red-500' : 'grid'}>
                                    <span onClick={() => setislogin(true)} className='fa fa-user'></span>
                                    <p className='text-[12px]'>Log in</p>
                                </div>
                            </div>
                        </div>
                    </div>}
                </div>
            }
        </>
    )
}
